.promo{
    min-height: 100vh;
    position: relative;
    background: rgb(192, 245, 255) no-repeat 50%/cover;
    padding-top: 205px;
    &__title{
        width: 535px;
        margin-top: 10px;
    }
    &__subtitle{
        position: relative;
        padding-left: 10px;
        z-index: 2;
        

    }
    &__btns{
        margin-top: 25px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        width: 250px;
    }
    &__link{
        color: #000;
        fwb
        &:hover{
            color: #000;
            text-decoration: none;
        }
    }
    &__code{
        position: absolute;
        right: 320px;
        top: 300px;
    }
}